body{
  margin: 0;
}

.navBar{
    width: 88%;
    padding: 0 6%;
    height: 13vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navBar-brand{
    display: flex;
}

.svnit-logo{
    /* display: none; */
    height: 57px;
    width: auto;
}

a.drishti-contain{
    padding-top: 0.5vh;
    padding-left: 14px;
}

.drishti-logo{
    height: 54px;
    width: auto;
}

.navBar-menu{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navBar-menu a{
    text-decoration: none;
    font-size: 1em;
    font-weight: 600;
    color: #f0f0ff;
    border-bottom: 0.16em solid transparent;
    font-family: 'Lato', 'Open Sans', sans-serif;
    padding: 0.25em 10px;
    margin-left: 35px;
    text-transform: uppercase;
    transition: 0.3s ease;
}

.navBar-menu a:hover{
    border-bottom: 0.16em solid #ffec44;
    color: #ffec44;
    transition: all 0.25s ease;
}
.first-div{
    height: 100vh;
    width: 100%;
    background-image: url(/drishti-website-revamped/static/media/section_1_back_landscape.bab93678.webp);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.container{
    width: 100%;
    height: 87vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
}


/* Left Section */


.left-sec{
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.robotic{
    position: relative;
    max-height: 100%;
    min-height: 42vw;
}
.robotic img.robo-hand{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 94%;
    z-index: 50;
}
.robotic img.index-circle{
    position: absolute;
    left: 78.5%;
    bottom: 82%;
    width: 18%;
    z-index: 60;
}
.robotic img.quadrilateral{
    position: absolute;
    left: 16%;
    bottom: 56%;
    width: 38%;
    z-index: 40;
}
.robotic img.thumb-circle{
    position: absolute;
    left: 51%;
    bottom: 22%;
    width: 25%;
    z-index: 40;
}
.robotic img.striped{
    position: absolute;
    left: 10%;
    bottom: 42%;
    width: 23%;
    z-index: 40;
}



/* Animations */
.translate-robo{
    animation: move-right 0.6s ease-in;
    -webkit-animation: move-right 0.6s ease-in;
}
@-webkit-keyframes move-right{
    from { opacity: 0; transform: translateX(-100%); -webkit-transform: translateX(-100%); -moz-transform: translateX(-100%); -ms-transform: translateX(-100%); -o-transform: translateX(-100%); }
    to { opacity: 1; transform: translateX(0); -webkit-transform: translateX(0); -moz-transform: translateX(0); -ms-transform: translateX(0); -o-transform: translateX(0); }
}
@keyframes move-right{
    from { opacity: 0; transform: translateX(-100%); -webkit-transform: translateX(-100%); -moz-transform: translateX(-100%); -ms-transform: translateX(-100%); -o-transform: translateX(-100%); }
    to { opacity: 1; transform: translateX(0); -webkit-transform: translateX(0); -moz-transform: translateX(0); -ms-transform: translateX(0); -o-transform: translateX(0); }
}
.zoom-index{
    animation: zoom 2.4s ease;
    -webkit-animation: zoom 2.4s ease;
}
@-webkit-keyframes zoom{
    0%{ opacity: 0;}
    65%{ width: 0%; opacity: 0;}
    100%{ width: 18%; opacity: 1;}
}
@keyframes zoom{
    0%{ opacity: 0;}
    65%{ width: 0%; opacity: 0;}
    100%{ width: 18%; opacity: 1;}
}
.rotate-quad{
    animation: rotate-quad 2.4s ease-in;
    -webkit-animation: rotate-quad 2.4s ease-in;
}
@-webkit-keyframes rotate-quad{
    0%{ opacity: 0;}
    28%{ opacity: 0; transform: rotate(-24deg) translateX(-10%); -webkit-transform: rotate(-24deg) translateX(-10%); -moz-transform: rotate(-24deg) translateX(-10%); -ms-transform: rotate(-24deg) translateX(-10%); -o-transform: rotate(-24deg) translateX(-10%); }
    75%{ opacity: 1; transform: rotate(0deg) translateX(0); -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -ms-transform: rotate(0deg); -o-transform: rotate(0deg); }
}
@keyframes rotate-quad{
    0%{ opacity: 0;}
    28%{ opacity: 0; transform: rotate(-24deg) translateX(-10%); -webkit-transform: rotate(-24deg) translateX(-10%); -moz-transform: rotate(-24deg) translateX(-10%); -ms-transform: rotate(-24deg) translateX(-10%); -o-transform: rotate(-24deg) translateX(-10%); }
    75%{ opacity: 1; transform: rotate(0deg) translateX(0); -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -ms-transform: rotate(0deg); -o-transform: rotate(0deg); }
}
.translate-thumb{
    animation: move-up 2.4s ease-in;
    -webkit-animation: move-up 2.4s ease-in;
}
@-webkit-keyframes move-up{
    0%{ opacity: 0;}
    25%{ opacity: 0; transform: translateY(30%); -webkit-transform: translateY(30%); -moz-transform: translateY(30%); -ms-transform: translateY(30%); -o-transform: translateY(30%); }
    60%{ opacity: 1; transform: translateY(0); -webkit-transform: translateY(0); -moz-transform: translateY(0); -ms-transform: translateY(0); -o-transform: translateY(0); }
}
@keyframes move-up{
    0%{ opacity: 0;}
    25%{ opacity: 0; transform: translateY(30%); -webkit-transform: translateY(30%); -moz-transform: translateY(30%); -ms-transform: translateY(30%); -o-transform: translateY(30%); }
    60%{ opacity: 1; transform: translateY(0); -webkit-transform: translateY(0); -moz-transform: translateY(0); -ms-transform: translateY(0); -o-transform: translateY(0); }
}
.rotate-fade-strip{
    animation: rotate-fade 2.4s ease-in;
    -webkit-animation: rotate-fade 2.4s ease-in;
}
@-webkit-keyframes rotate-fade{
    0%{ opacity: 0;}
    55%{ opacity: 0; transform: rotate(22deg); -webkit-transform: rotate(22deg); -moz-transform: rotate(22deg); -ms-transform: rotate(22deg); -o-transform: rotate(22deg); }
    90%{ opacity: 1; transform: rotate(0deg); -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -ms-transform: rotate(0deg); -o-transform: rotate(0deg); }
}
@keyframes rotate-fade{
    0%{ opacity: 0;}
    55%{ opacity: 0; transform: rotate(22deg); -webkit-transform: rotate(22deg); -moz-transform: rotate(22deg); -ms-transform: rotate(22deg); -o-transform: rotate(22deg); }
    90%{ opacity: 1; transform: rotate(0deg); -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -ms-transform: rotate(0deg); -o-transform: rotate(0deg); }
}




/* Right Section */


.right-sec{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.title{
    margin-left: 4em;
}
.title h1{
    font-family: 'Anonymous Pro', monospace;
    font-weight: 700;
    color: #f8fbfd;
    font-size: 8em;
    text-transform: uppercase;
    margin: 0;
}
.title .typed{
    display: block;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 400;
    color: #f8fbfd;
    font-size: 2em;
    margin: 0.3em 0;
}


/* Explore button */


.title a.explore{
    margin-top: 3.8em;
    display: inline-block;
    text-decoration: none;
    color: #f8fbfd;
    background-color: #dc3c4d;
    font-size: 1.1em;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    padding: 8px 24px 10px 34px;
    border-radius: 1.4em;
    position: relative;
}
.title a.explore .down{
    position: absolute;
    top: 1.25em;
    transform: translate(-70%, -50%);
    -webkit-transform: translate(-70%, -50%);
    -moz-transform: translate(-70%, -50%);
    -ms-transform: translate(-70%, -50%);
    -o-transform: translate(-70%, -50%);
}
.title a.explore:hover > .down{
    -webkit-animation: up-down 0.8s linear infinite;
            animation: up-down 0.8s linear infinite;
}


/* Notices buttton link */


.title a.notices{
    margin-top: 10px;
    margin-bottom: 2.5em;
    display: inline-block;
    text-decoration: none;
    color: #dc3c4d;
    background-color: #f8fbfd;
    font-size: 1.1em;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    padding: 4px 5px 10px 34px;
    border-radius: 1.4em;
} 
.title a.notices .imp{
    font-size: 2em;
    margin-bottom: -0.18em;
}
.title a.notices:hover > .imp{
    -webkit-animation: rotate 1.4s linear infinite;
            animation: rotate 1.4s linear infinite;
}



/* Keyframes for all the animations used above */


.blink{
    -webkit-animation: blink 2.4s linear infinite;
            animation: blink 2.4s linear infinite;
}
@-webkit-keyframes blink{
    0%{opacity: 0.1;}
    30%{opacity: 1;}
    50%{opacity: 1;}
    70%{opacity: 0.6;}
    100%{opacity: 0.1;}
}
@keyframes blink{
    0%{opacity: 0.1;}
    30%{opacity: 1;}
    50%{opacity: 1;}
    70%{opacity: 0.6;}
    100%{opacity: 0.1;}
} 
@-webkit-keyframes rotate{
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
} 
@keyframes rotate{
    from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
}
@-webkit-keyframes up-down{
    0%{ transform: translate(-70%, -50%); 
        -webkit-transform: translate(-70%, -50%); 
        -moz-transform: translate(-70%, -50%); 
        -ms-transform: translate(-70%, -50%); 
        -o-transform: translate(-70%, -50%); }
    20%{ transform: translate(-70%, -30%); 
        -webkit-transform: translate(-70%, -30%); 
        -moz-transform: translate(-70%, -30%); 
        -ms-transform: translate(-70%, -30%); 
        -o-transform: translate(-70%, -30%); }
    85%{ transform: translate(-70%, -60%); 
        -webkit-transform: translate(-70%, -60%); 
        -moz-transform: translate(-70%, -60%); 
        -ms-transform: translate(-70%, -60%); 
        -o-transform: translate(-70%, -60%); }
    1000%{ transform: translate(-70%, -50%); 
        -webkit-transform: translate(-70%, -50%); 
        -moz-transform: translate(-70%, -50%); 
        -ms-transform: translate(-70%, -50%); 
        -o-transform: translate(-70%, -50%); }
}
@keyframes up-down{
    0%{ transform: translate(-70%, -50%); -webkit-transform: translate(-70%, -50%); -moz-transform: translate(-70%, -50%); -ms-transform: translate(-70%, -50%); -o-transform: translate(-70%, -50%); }
    20%{ transform: translate(-70%, -30%); -webkit-transform: translate(-70%, -30%); -moz-transform: translate(-70%, -30%); -ms-transform: translate(-70%, -30%); -o-transform: translate(-70%, -30%); }
    85%{ transform: translate(-70%, -60%); -webkit-transform: translate(-70%, -60%); -moz-transform: translate(-70%, -60%); -ms-transform: translate(-70%, -60%); -o-transform: translate(-70%, -60%); }
    1000%{ transform: translate(-70%, -50%); -webkit-transform: translate(-70%, -50%); -moz-transform: translate(-70%, -50%); -ms-transform: translate(-70%, -50%); -o-transform: translate(-70%, -50%); }
}





/* Responsive section */


@media only screen and (max-width: 768px) {
    .first-div{
        background-image: url(/drishti-website-revamped/static/media/section_1_back_portrait.a4bd5986.webp);
    }  
}

